@import url('https://fonts.googleapis.com/css2?family=Dosis:wght@200&family=Josefin+Sans:wght@100&family=Nova+Round&display=swap');
$font-1:'Nova Round',cursive;
$font-2:'Dosis', Sans-serif;
$font-3:'Josefin Sans', serif;

$couleur-1:#a509ff;
$couleur-2:#a509ff;
// $couleur-2:#f46c8e;
$couleur-3: rgb(4, 184, 255);
// $couleur-3: rgb(99,164,233);
$couleur-4:#ff0062;
$couleur-5:#ffffff;
$couleur-6:#ffffff;
$white:#ddd;
$black:#333;
$col1:#000;

$borderRadius:5px;
$shadow:0 1px 3px #333;
$min-height:95vh;

@mixin  verticalCenter {
    display: flex;
    justify-content: center;
    align-items: center;
}
* {
    box-sizing: 0;
    padding: 0;
}
body{
    margin:11px 11px;
    max-width: auto;
    color:$black;
    font-family: $font-1;
}
button{
    cursor: pointer;
}
li{
    list-style-type: none;
}
a{
    text-decoration: none;
    color: $black;
}
a:visited{
    color: $black;
}
.home, .knowledges, .portfolio,.contact{
    display:flex;
    @media screen and (max-width:850px){
        display: block !important;
    }
}
.homeContent, .knowledgesContent, .portfolioContent,.contactContent{
    margin-left: .4rem;
    @media screen and(max-width:850px){
        margin-left: 0 !important;
    }
}